import { Utils } from "hornet-js-utils";
import { Logger } from "hornet-js-utils/src/logger";
import { Class } from "hornet-js-utils/src/typescript-utils";
import { BusinessError } from "hornet-js-utils/src/exception/business-error";
import { BusinessErrorList } from "hornet-js-utils/src/exception/business-error-list";
import { IHornetPage } from "hornet-js-components/src/component/ihornet-page";
import { NotificationManager } from "src/notification/notification-manager";
import { fireHornetEvent } from "src/event/hornet-event";
import { COMPONENT_CHANGE_EVENT } from "src/routes/router-client-async-elements";

const logger: Logger = Utils.getLogger("hornet-js-core.exception.error-manager");

/**
 * Applique le traitement approprit  l'erreur indique suivant sa nature :
 * - erreur fonctionnelle (BusinessError) : dclenche un vnement d'ajout de notification
 * - erreur technique : dclenche l'vnement de changement de page vers la page d'erreur indique
 * @param error erreur  traiter
 * @param errorPage page  afficher en cas d'erreur technique
 * @param method contexte ventuel de l'erreur : il s'agit de la fonction dans laquelle l'erreur s'est produite
 */
export function manageError(error:any, errorPage: Class<IHornetPage<any,any>>, method?: string) {
    let notify:boolean = error instanceof BusinessError && method != "render";
    if (notify) {
        let exceptions:BusinessError[];
        if (error instanceof BusinessErrorList) {
            exceptions = (error as BusinessErrorList).getErrors();
        } else {
            exceptions = [error];
        }
        NotificationManager.notify(null, null, null, exceptions);
    } else {
        Utils.setCls("hornet.currentError", error);
        fireHornetEvent(COMPONENT_CHANGE_EVENT.withData({newComponent: errorPage, data:  {}}));
    }
}


