import { Register } from "src/common-register";

class BrowserNameSpace {
    content: any = {};

    get(key: string) {
        return this.content[key];
    }

    set(key: string, value: any) {
        this.content[key] = value;
    }
}

class BrowserContinuationLocalStorage {
    static NAMESPACES: any = {};

    /**
     * Fonction retournant un namespace, peut-tre undefined si le namespace n'existe pas
     * @param localStorageName Nom du localStorage
     * @return {any}
     */
    static getNamespace(localStorageName: string): any {
        return BrowserContinuationLocalStorage.NAMESPACES[localStorageName];
    }


    /**
     * Fonction crant et retournant un namespace
     * @param localStorageName Nom du localStorage
     * @return {any}
     */
    static createNamespace(localStorageName: string): any {
        let ns = new BrowserNameSpace();
        BrowserContinuationLocalStorage.NAMESPACES[localStorageName] = ns;
        return ns;
    }
}

export class ContinuationLocalStorage {
    /**
     * Fonction retournant le continuationlocalstorage hornet ou un storage applicatif
     * @param localStorageName Nom du localStorage, par dfaut HornetContinuationLocalStorage
     * @return {any}
     */
    static getContinuationStorage(localStorageName: string = "HornetContinuationLocalStorage"): any {
        let cls;
        if (Register.isServer) {
            cls = require("continuation-local-storage");
        } else {
            cls = BrowserContinuationLocalStorage;
        }

        return cls.getNamespace(localStorageName) || cls.createNamespace(localStorageName);
    }

    static get(key: string, localStorageName?: string) {
        return ContinuationLocalStorage.getContinuationStorage(localStorageName || "HornetContinuationLocalStorage").get(key);
    }

    static set(key: string, value: any, localStorageName?: string) {
        ContinuationLocalStorage.getContinuationStorage(localStorageName || "HornetContinuationLocalStorage").set(key, value);
    }
}


