import { Utils } from "hornet-js-utils";
import { Logger } from "hornet-js-utils/src/logger";
import * as Promise from "hornet-js-utils/src/promise-api";

var OnionSkin = require("onionskin/src/browser");
const logger: Logger = Utils.getLogger("hornet-js-core.cache.hornet-cache");

/**
 * Cache de l'application.
 * Voir documentation Onion Skin: http://onionskin.io/
 * */
export class HornetCache {

    private static _instance:HornetCache = new HornetCache();

    private pool:any;

    constructor() {
        if (HornetCache._instance) {
            throw new Error("Error: Instantiation failed: Use HornetCache.getInstance() instead of new.");
        }
        var ephemeral = new OnionSkin.Drivers.Ephemeral();
        this.pool = new OnionSkin([ephemeral]);

        HornetCache._instance = this;
    }

    public static getInstance():HornetCache {
        return HornetCache._instance;
    }

    /**
     * Item voir documentation de Onion Skin http://onionskin.io/api/
     * @param key cl de l'item
     * @returns {any} Item comme dfinis dans l'API de Onion Skin
     */
    getItem(key:string):Promise<any> {
        return this.pool.get(key);
    }

    /**
     * Mise en cache de la donne passe en paramtre. Methode Asynchrone: Promise.
     * @param key cl de la valeur en cache
     * @param data donnes  mettre en cache
     * @param timetoliveInCache temps de sauvegarde de la donnes
     */
    setCacheAsynchrone(key:string, data:any, timetoliveInCache?:number):Promise<any> {
        return this.pool.getItem(key).set(data, timetoliveInCache);
    }

    /**
     * Supprime du cache de la donne passe en paramtre. Methode Asynchrone: Promise.
     * @param key cl de la valeur en cache
     */
    clearCacheAsynchrone(key:string):Promise<any> {
        return this.pool.getItem(key).clear();
    }
}



